export default {
	props:['city', 'letter'],
	data() {
		return {
			list:['深圳','北京','上海','成都','广州','重庆','西安','苏州','武汉','杭州','郑州','南京'],
			viewId: '',
			currentCity: "广东"
		};
	},
	methods: {
		click(city) {
			uni.setStorage({
				key: "currentCity",
				data: city
			})
			uni.getStorage({
				key: "currentCity",
				/*
					注意这里需要箭头函数,这样this的指向才会指向该vue实例,
					如果不是箭头函数，this指向的是window,而window中没有currentCity这个属性
				 */
				success: (res) => {
					this.currentCity = res.data;
				}
			})
			uni.redirectTo({
				url: "/pages/city/city"
			})
		}
	},
	watch: {
		letter() {
			setTimeout(() => {
				this.viewId = this.letter;
			}, 1);
		}
	}
}
